import errorStackParser from 'error-stack-parser';
import stackGenerator from 'stack-generator';

function noop() { }

let email;
let siteId;

function isShapedLikeParsableError(err) {
  return err.stack || err['opera#sourceloc']; // ?
}

function fromError(err) {
  return errorStackParser.parse(err);
}

function generateArtifically() {
  return stackGenerator.backtrace();
}

function upload(data, callback) {
  const uploadUrl = window.uploadUrl;
  if (!uploadUrl) return;
  const request = new XMLHttpRequest();
  request.onerror = callback;
  request.onreadystatechange = function onreadystatechange() {
    if (request.readyState === 4) {
      if (request.status === 200) {
        callback(null, request.responseText);
      } else {
        callback(new Error(`POST to ${uploadUrl} failed with status: ${request.status}`));
      }
    }
  };

  request.open('post', uploadUrl);

  request.setRequestHeader('Content-Type', 'application/json');
  request.send(JSON.stringify(data));
}

function send(type, data) {
  upload({
    type,
    data,
    meta: {
      timestamp: Date.now(),
      userAgent: window.navigator.userAgent,
      href: window.location.href,
      email,
      siteId,
    },
  }, noop);
}

const sendError = send.bind(null, 'error');
export const track = send.bind(null, 'track');

function parseStack(e) { // use stack-frame library instead
  const stack = isShapedLikeParsableError(e) ? fromError(e) : generateArtifically();
  return stack.map(stackframe => stackframe.toString()).join(';');
}

export function error(e, source, line, column) {
  sendError({
    message: e.message,
    stack: parseStack(e),
    source,
    line,
    column,
  });
}

export function set(givenEmail, givenSiteId) {
  email = givenEmail;
  siteId = givenSiteId;
}

window.onerror = function (msg, url, line, column, err) {
  window.monitor.error(
    err,
    url,
    line,
    column || (window.event && window.event.errorCharacter) || 0
  );

  // Hide error in production mode
  return true;
};
window.addEventListener('unhandledrejection', (event) => {
  window.monitor.error(event.reason, 'unhandledrejection');
});



